<template>
  <div>
    <NotAuth>
      <template v-slot:error></template>
      <template v-slot:loading></template>
      <template v-slot:default>
        <div class="edit__wrapper d-flex flex-row">
          <div style="margin-top: 80px; margin-left: 20px;">
            <Panel />
          </div>
          <v-card class="edit">
            <div
              v-if="$store.state.isRuleError.length <= 0 && tab == 1"
              class="edit__block d-flex flex-row align-center"
            >
              <v-card-title class="edit__title">Основные сведения</v-card-title>
              <v-btn disabled class="edit__data"
                >Дата регистрации - {{ dataDB }}</v-btn
              >
            </div>
            <v-card-title v-if="tab == 2" class="edit__title">
              Дополнительные сведения
            </v-card-title>
            <div class="edit__middle d-flex flex-row justify-space-between">
              <editInfo v-if="tab == 1" />
              <editAdditionally v-else-if="tab == 2" />
              <editFamily v-else-if="tab == 3" />
              <BlackList v-else-if="tab == 4" />

              <div class="edit__right d-flex flex-column">
                <v-btn @click="tab = 1" class="edit__tab" color="myPurple">
                  Основные сведения
                </v-btn>
                <v-btn @click="tab = 2" class="edit__tab" color="myPurple">
                  Дополнительные сведения
                </v-btn>
                <v-btn @click="tab = 4" class="edit__tab" color="myPurple">
                  Черный список
                </v-btn>
              </div>
            </div>
          </v-card>
        </div>
      </template>
    </NotAuth>
  </div>
</template>

<script>
import Panel from "../components/Panel.vue";
import editAdditionally from "../components/editAdditionally.vue";
import editInfo from "../components/editInfo.vue";
import BlackList from "../components/BlackList.vue";
import NotAuth from "../components/NotAuth.vue";
import "../scss/edit.scss";

export default {
  name: "Edit",
  metaInfo: {
    title: "MyOcean - Мои настройки"
  },
  components: {
    Panel,
    editAdditionally,
    editInfo,
    BlackList,
    NotAuth,
  },
  data() {
    return {
      tab: 1,
      dataDB: "",
      user: "",
    };
  },
  created() {
    if (localStorage.getItem("user")) this.user = JSON.parse(localStorage.getItem("user"));
    this.dataDB = this.user.date
  },
};
</script>
