<template>
  <div>
    <div class="edit__info">
      <v-text-field
        type="number"
        color="myPurple"
        class="edit__info__input"
        label="Возраст"
        v-model="profile.additionally.age"
      >
      </v-text-field>
      <v-textarea
        color="myPurple"
        class="edit__info__input edit__interesting"
        label="Деятельность"
        auto-grow
        rows="1"
        v-model="profile.additionally.development"
      >
      </v-textarea>
      <v-textarea
        color="myPurple"
        class="edit__info__input edit__interesting"
        label="Интересы"
        auto-grow
        rows="1"
        v-model="profile.additionally.interesting"
      >
      </v-textarea>
      <v-textarea
        color="myPurple"
        class="edit__info__input edit__interesting"
        label="Любимая музыка и исполнители"
        auto-grow
        rows="1"
        v-model="profile.additionally.music"
      >
      </v-textarea>
      <v-textarea
        color="myPurple"
        class="edit__info__input edit__interesting"
        label="Любимые фильмы, сериалы, телешоу:"
        auto-grow
        rows="1"
        v-model="profile.additionally.films"
      >
      </v-textarea>
      <EditCategory 
        :user="dataDB" 
        @categoryDelete="categoryDelete" 
        @categoryAdd="categoryAdd" 
      />
    </div>
    <v-btn 
      color="myPurple" 
      style="color: #fff !important; margin-left: 20px; margin-bottom: 30px" 
      @click="saveData"
    >
      Сохранить
    </v-btn>
    <v-snackbar v-model="isSave" :timeout="timer">
      <h4>Ваши сведения сохранены. Для просмотра обновите страницу.</h4>
      <v-btn 
        color="myPurple" 
        @click="isSave = false" 
        style="color: #fff !important;">
        Закрыть
      </v-btn>
    </v-snackbar>
    <v-snackbar v-model="isCategoryAdd" :timeout="timer">
      <p>Категория добавлена, не забудьте ее сохранить</p>
      <v-btn 
        color="myPurple" 
        @click="isCategoryAdd = false" 
        style="color: #fff !important;">
        Закрыть
      </v-btn>
    </v-snackbar>
    <v-snackbar v-model="isNotLength" :timeout="timer">
      <h4>Ваши поля должны быть заполнены</h4>
      <v-btn 
        color="myPurple" 
        @click="isNotLength = false" 
        style="color: #fff !important;">
        Закрыть
      </v-btn>
    </v-snackbar>
  </div>
</template>

<script>
import EditCategory from "../components/EditCategory.vue";
import axios from "axios"

export default {
  name: "editAdditionally",
  components: { EditCategory },
  data() {
    return {
      dataDB: "",
      isNotLength: false,
      isCategoryAdd: false,
      profile: {
        additionally: {
          age: "",
          development: "",
          interesting: "",
          music: "",
          films: "",
          categories: []
        }
      },
      isSave: false,
      timer: 2000
    }
  },
  async created() {
    this.dataDB = JSON.parse(localStorage.getItem("user"))
    await axios.get("/api/auth/test", 
      { headers: { Authorization: localStorage.getItem("jwt") }})

    this.profile.additionally.age = this.dataDB.additionally.age
    this.profile.additionally.development = this.dataDB.additionally.development
    this.profile.additionally.interesting = this.dataDB.additionally.interesting
    this.profile.additionally.music = this.dataDB.additionally.music
    this.profile.additionally.films = this.dataDB.additionally.films
    this.profile.additionally.categories = this.dataDB.additionally.categories
  },
  methods: {
    async saveData() {
      this.isCategoryAdd = false
      let response = await axios.put("/api/users/additionally_add/" + 
      this.dataDB.id, this.profile)
      localStorage.setItem("user", JSON.stringify(response.data))
      this.isSave = true
    },
    categoryAdd(data) {
      this.profile.additionally.categories.push(data)
      this.isCategoryAdd = true
    },
    categoryDelete(data) {
      for (let i = 0; i < this.profile.additionally.categories.length; i++) {
        if (this.profile.additionally.categories[i].title == data.title &&
        this.profile.additionally.categories[i].text == data.text) {
          this.profile.additionally.categories.splice(i, 1)
        }
      }
    }
  }
};
</script>
<style lang="scss">
.edit {
  &__interesting {
    width: 500px !important;
  }
}
</style>