<template>
  <div class="d-flex flex-row">
    <div class="edit__info">
      <form @submit.prevent="dataSend" enctype="multipart/form-data" method="post">
      <div class="d-flex flex-row">
        <v-text-field
          class="edit__firstname edit__info__input"
          color="myPurple"
          label="Ваше имя"
          v-model="profile.firstname"
        ></v-text-field>
        <v-text-field
          class="edit__lastname"
          color="myPurple"
          label="Ваше фамилия"
          v-model="profile.lastname"
        ></v-text-field>
      </div>
      <div class="d-flex flex-row">
        <v-text-field
          class="edit__country edit__info__input"
          label="Ваша страна проживания"
          color="myPurple"
          v-model="profile.additionally.country"
        ></v-text-field>
        <v-text-field
          class="edit__city"
          label="Ваш город проживания"
          color="myPurple"
          v-model="profile.additionally.city"
        ></v-text-field>
      </div>
      <div class="d-flex flex-row align-center">
        <v-menu offset-y>
            <template v-slot:activator="{ on }">
              <v-btn class="edit__menu" color="myPurple" dark v-on="on">
                Укажите ваш пол
              </v-btn>
            </template>
            <v-list>
            <v-list-item>
                <v-list-item-title 
                  style="cursor: pointer;" 
                  @click="profile.additionally.gender = 'Мужской'"
                >Мужской</v-list-item-title>
            </v-list-item>
            <v-list-item>
                <v-list-item-title 
                  style="cursor: pointer;" 
                  @click="profile.additionally.gender = 'Женский'"
                >Женский</v-list-item-title>
            </v-list-item>
            </v-list>
        </v-menu>
        <v-text-field label="Пол" 
        disabled 
        v-model="profile.additionally.gender"></v-text-field>
      </div>
      <v-text-field
        class="edit__phone edit__info__input"
        label="Ваш телефон"
        v-model="profile.additionally.phone"
        color="myPurple"
      ></v-text-field>
      <v-textarea
        label="Расскажите о себе"
        color="myPurple"
        class="edit__info__input edit__info__textarea"
        v-model="profile.additionally.about"
        auto-grow
        rows="4"
        outlined
      ></v-textarea>
      <v-btn class="edit__info__save" type="submit" color="myPurple">Сохранить</v-btn>
    </form>
    </div>
    <div class="edit__image d-flex align-center flex-column">
      <v-img 
        class="edit__image__logo" 
        v-if="isImg && dataDB.additionally.img && !preImg" 
        :src="getFile"
      >
      </v-img>
      <v-img 
        class="edit__image__logo" 
        v-else-if="preImg"
        :src="preImg"
      ></v-img>
      <div v-else class="edit__image__logo"></div>
      <form>
        <input 
          id="upload"
          class="edit__file" 
          type="file"
          @input="getImage" 
          placeholder="Вставить изображение" 
        />
        <label class="edit__file__label" for="upload">
          <div>
            <v-icon style="margin-bottom: 5px;">save</v-icon>
            Загрузить изображение
          </div>
        </label>
        <p v-if="isFile && img.name" class="edit__file__load">Загружено {{ img.name }}</p>
      </form>
    </div>
    <v-snackbar v-model="isSave" :timeout="timer">
      <h4 class="edit__info__text">Ваши сведения сохранены. Для просмотра обновите страницу.</h4>
      <v-btn color="myPurple" @click="isSave = false" style="color: #fff !important;">
        Закрыть
      </v-btn>
    </v-snackbar>
  </div>
</template>

<script>
import axios from "axios";
import "../scss/editInfo.scss";

export default {
  name: "editInfo",
  data() {
    return {
      gender: "",
      dataDB: "",
      profile: {
        firstname: "",
        lastname: "",
        additionally: {
          country: "",
          city: "",
          gender: "",
          phone: "",
          about: "",
          img: ""
        }
      },
      timer: 2000,
      img: null,
      preImg: null,
      isSave: false,
      imgSrc: null,
      isImg: false,
      isFile: false
    }
  },
  async created() {
    this.dataDB = JSON.parse(localStorage.getItem("user"))
    try {
      await axios.get("/api/auth/test", { headers: 
        { Authorization: localStorage.getItem("jwt") }})
      this.profile.firstname = this.dataDB.firstname
      this.profile.lastname = this.dataDB.lastname
      this.profile.additionally.country = this.dataDB.additionally.country
      this.profile.additionally.city = this.dataDB.additionally.city
      this.profile.additionally.gender = this.dataDB.additionally.gender
      this.profile.additionally.phone = this.dataDB.additionally.phone
      this.profile.additionally.about = this.dataDB.additionally.about
      this.profile.additionally.img = this.dataDB.additionally.img
      this.isImg = true
    } catch(err) {
      console.log(err.message)
    }
  },
  computed: {
    getFile() {
      return "/" + this.dataDB.additionally.img
    }
  },
  methods: {
    async dataSend() {
      let formData = new FormData()
      if (this.img) formData.set("img", this.img, this.img.name)
      formData.set("firstname", this.profile.firstname)
      formData.set("lastname", this.profile.lastname)
      formData.set("country", this.profile.additionally.country)
      formData.set("city", this.profile.additionally.city)
      formData.set("gender", this.profile.additionally.gender)
      formData.set("phone", this.profile.additionally.phone)
      formData.set("about", this.profile.additionally.about)
      
      let response = await axios.put('/api/users/info_add/' + this.dataDB.id, formData,
      { headers: { "Content-Type": "multipart/form-data" }})
      await axios.put("/api/comments/info_replace/" + this.dataDB.id, formData)

      localStorage.setItem("user", JSON.stringify(response.data))
      this.isSave = true
    },
    getImage(event) {
      this.img = event.target.files[0]
      this.isFile = true
      if (this.img) {
        let reader = new FileReader()
        reader.onload = e => this.preImg = e.target.result
        reader.readAsDataURL(event.target.files[0])
      }
    }
  },
};
</script>
