<template>
  <div>
    <div v-if="categoryList.length > 0">
      <div class="d-flex flex-row" v-for="(category, index) of categoryList" :key="index">
        <v-textarea
        color="myPurple"
        class="edit__info__input edit__interesting"
        :label="category.title"
        auto-grow
        rows="1"
        :value="category.text"
        outlined
        readonly
      ></v-textarea>
        <v-btn
          class="mx-2"
          color="error"
          small
          fab
          style="margin-top: 10px;"
          @click="isDelete = true; $emit('categoryDelete', category)"
        >
          <v-icon>mdi-cancel</v-icon>
        </v-btn>
      </div>
    </div>
    <v-btn
      class="edit__info__add"
      v-if="!isCategory && categoryList.length < 6"
      @click="categoryOpen"
      outlined
      large
      color="teal"
      dark
    >
      <v-icon style="margin-bottom: 10px" size="32" class="edit__info__add__icon">+</v-icon>
      Добавить свою категорию
    </v-btn>
    <v-btn
      class="edit__info__add"
      v-if="!isCategory && categoryList.length > 6"
      disabled
      outlined
      title="Максимальное количество категорий - 5"
      large
      color="teal"
    >
      <v-icon size="42" class="edit__info__add__icon">+</v-icon>
      Добавить свою категорию
    </v-btn>
    <div class="d-flex flex-row" v-if="isCategory">
      <v-text-field
        solo
        v-model="category.title"
        placeholder="Название вашей категории"
        class="edit__info__add__category"
      ></v-text-field>
      <v-textarea
        auto-grow
        rows="1"
        solo
        v-model="category.text"
        placeholder="Описание вашей категории"
        class="edit__info__add__textarea"
      ></v-textarea>
      <div class="d-flex flex-column" style="margin-left: 20px;">
        <v-btn
          class="mx-2"
          @click="categoryAdd"
          color="tertiary"
          fab
          style="color: #fff"
        >
          <v-icon>mdi-plus</v-icon>
        </v-btn>
        <v-btn
          class="mx-2"
          @click="isCategory = false"
          color="error"
          fab
          style="color: #fff; margin-top: 20px;"
        >
          <v-icon>mdi-cancel</v-icon>
        </v-btn>
      </div>
    </div>
    <v-snackbar v-model="isDelete" :timeout="timer">
      <p>Категория удалена. Не забудьте сохранить изменения</p>
      <v-btn 
        color="myPurple" 
        @click="isDelete = false" 
        style="color: #fff !important;">
        Закрыть
      </v-btn>
    </v-snackbar>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "EditCategory",
  data() {
    return {
      isCategory: false,
      category: {
        title: "",
        text: ""
      },
      categoryList: [],
      isDelete: false,
      timer: 2000
    }
  },
  props: {
    user: Object
  },
  async created() {
    try {
      let res = await axios.get("/api/users/get_categories/" + this.user.id)
      this.categoryList = res.data
    } catch(err) {
      console.log(err.message)
    }
  },
  methods: {
    categoryAdd() {
      if (this.category.text.length > 0 && this.category.title.length > 0) {
        this.$emit("categoryAdd", { title: this.category.title, text: this.category.text })
        this.isCategory = false
      } else this.isNotLength = true
    },
    categoryOpen() {
      this.category.title = ""
      this.category.text = ""
      this.isCategory = true
    }
  }
};
</script>

<style lang="scss" scoped>
.edit {
  &__info {
    &__add {
      margin-bottom: 20px;
      margin-left: 20px;
      width: 500px;
      &__icon {
        margin-right: 20px;
      }
      &__category {
        width: 300px;
        margin-left: 20px;
      }
      &__textarea {
        margin-left: 30px;
        width: 400px;
      }
    }
  }
}
</style>