<template>
  <div>
    <div class="edit__blacklist">
      <h2>Черный список</h2>
      <div v-if="blackList.length > 0">
        <div class="edit__blacklist__users" v-for="(user, index) of blackList" :key="index">
          <div class="edit__blacklist__user d-flex flex-row align-center">
            <v-img 
              v-if="user.additionally.img" 
              class="edit__blacklist__logo"
              :src="'/' + user.additionally.img"
            ></v-img>
            <div v-else class="edit__blacklist__logo"></div>
            <div class="edit__blacklist__title d-flex flex-column">
              <router-link 
                :to="{name: 'Account', params: {id: user.id}}"
                class="edit__blacklist__title__name"
              >
                  {{ user.firstname }} {{ user.lastname }}
              </router-link>
              <v-btn color="error" class="edit__blacklist__title__btn" @click="userDelete(user)">
                  Удалить
              </v-btn>
            </div>
          </div>
        </div>
      </div>
      <div class="edit__blacklist__nolist d-flex flex-row align-center" v-else>
        <div style="width:500px;">
        <h2>Ваш черный список пуст.</h2>
          <p>Вы не сможете посмотреть информацию о пользователях, 
            которые находятся в черном списке</p>
        </div>
        <v-img width="200" height="200" src="../assets/night-owl.svg"></v-img>
      </div>
    </div>
  </div>
</template>

<script>
import { User } from "../models/models.js";
import axios from "axios";

export default {
  name: "BlackList",
  data() {
    return {
      user: "",
      blackList: []
    }
  },
  async created() {
    try {
      this.user = JSON.parse(localStorage.getItem("user"))
      
      let response = await axios.get("/api/users/find_user/" + this.user.id,
      { headers: { Authorization: localStorage.getItem("jwt") }})

      let user = new User(response.data)

      for (let item of user.blackList) {
        let res = await axios.get("/api/users/find_user/" + item,
        { headers: { Authorization: localStorage.getItem("jwt") }})
        this.blackList.push(res.data)
      }
    } catch(err) {
      if (err.message == "Request failed with status code 401" && 
        this.$store.state.isRuleError == "У вас нет прав для просмотра данной страницы") {
        this.$store.commit("noRules", "У вас нет прав для просмотра данной страницы")
      }
    }
  },
  methods: {
    async userDelete(user) {
      let res = await axios.delete("/api/users/blacklist_delete/" + 
        user.id + '/' + this.user.id)
      localStorage.setItem("user", JSON.stringify(res.data))
    }
  }
};
</script>

<style lang="scss" scoped>
.edit {
  &__blacklist {
    padding: 20px;
    h2 {
      margin-bottom: 50px;
      font-size: 24px;
    }
    &__user {
      margin-bottom: 50px;
    }
    &__logo {
      width: 64px;
      height: 64px;
      border-radius: 50%;
      background-color: #2cb67d;
      cursor: pointer;
    }
    &__title {
      margin-left: 20px;
      &__name {
        color: #7f5af0;
        font-size: 24px;
        text-decoration: none;
        cursor: pointer;
        font-weight: normal;
        &:hover {
          text-decoration: underline;
        }
      }
      &__btn {
          margin-top: 10px;
        color: #fff !important;
        width: 150px;
      }
    }
    &__nolist {
      h2 {
        font-size: 32px;
        margin-right: 20px;
        margin-top: 30px;
      }
    }
  }
}
</style>
